/* Set a background with a gradient to make it more appealing */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #2c3e50, #4ca1af); /* A nice gradient */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
}

/* Center the login form and add styling */
.App-header {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 300px;
}

h1 {
  color: #333; /* Strong dark grey to ensure visibility */
  margin-bottom: 20px;
}

/* Style the logo */
.company-logo {
  width: 150px;  /* Increased logo size */
  margin-bottom: 20px;
}

/* Style input fields */
input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 1rem;
  color: #333; /* Ensure the input text is also dark for better contrast */
}

/* Style labels */
label {
  display: block;
  text-align: left;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333; /* Strong dark grey for labels */
}

/* Style the button */
button {
  width: 100%;
  padding: 10px;
  background-color: #4ca1af; /* Complementary color to match the gradient */
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  background-color: #3b8d99; /* Slightly darker on hover */
}

/* Footer styling */
.footer {
  position: fixed;  /* Ensure the footer stays fixed at the bottom */
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;  /* Center the footer text */
  background-color: #282c34;
  color: #f0f0f0;  /* Light grey color to match the overall design */
  padding: 10px;
  font-size: 0.9rem;
}

.footer p {
  margin: 0;
}

/* Style for responsiveness */
@media (max-width: 768px) {
  .App-header {
    width: 90%;
  }
}

/* Style the error message */
p {
  color: #ff4c4c;  /* A red color for error messages */
  font-weight: bold;
}
